@font-face {
    font-family: "Blacker";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/Blacker/BlackerProDisplayTrial-Regular.woff2") format("woff2"),
    url("../fonts/Blacker/BlackerProDisplayTrial-Regular.woff") format("woff");
    font-display: swap;
}


@font-face {
    font-family: 'Roboto';
    src: url('Roboto-BlackItalic.eot');
    src: local('Roboto Black Italic'), local('Roboto-BlackItalic'),
        url('../fonts/Roboto-BlackItalic.woff2') format('woff2'),
        url('../fonts/Roboto-BlackItalic.woff') format('woff');
    font-weight: 900;
    font-style: italic;
}

